import LiveAnalyzer from "./live-analyzer";
import LiveChat from "./live-chat";

export default function LivePage() {
  return (
    <div style={{ padding: 16, maxWidth: 1180, margin: "0 auto" }}>
      <div style={{ display: "grid", gap: 6, marginBottom: 14 }}>
        <h1 style={{ fontSize: 22, fontWeight: 900, margin: 0 }}>Live Analyzer</h1>
        <p style={{ opacity: 0.75, margin: 0 }}>
          Scan/Plan uses <code>/api/analyze</code>. Chat uses <code>/api/chat</code>.
        </p>
      </div>

      <div style={{ display: "grid", gridTemplateColumns: "1fr 420px", gap: 16 }}>
        <LiveAnalyzer />
        <LiveChat />
      </div>
    </div>
  );
}

